body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: column;
          flex-direction: column;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

html,
body,
#root {
    width: 100%;
    height: 100%;
    box-sizing: border-box;

}

.root-container {
    width: 100%;
    height: 100%;
    display: -webkit-flex;
    display: flex;
    border: 7px solid rgb(27, 200, 212);
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    -webkit-flex-direction: column;
            flex-direction: column;
    
}
.containerhome{
    width: 100%;
    height: 100%;
    border: 7px solid rgb(27, 200, 212);
    background:salmon;
    float: left;
}
.box-controller {
    visibility: visible;
    display: -webkit-flex;
    display: flex;
    max-width: 24em;
    min-width: 21em;
    height: 40px;
    box-shadow: 0px 0px 20px 0px rgba(255, 14, 14, 0.2);
    margin-bottom: 30px;
    -webkit-align-items: center;
            align-items: center;
    -webkit-transition: visibility 0.5s ease-out;
    transition: visibility 0.5s ease-out;
    background-color: #6565e7;
}

.controller {
    -webkit-flex: 1 1;
            flex: 1 1;
    text-align: center;
    height: 100%;
    line-height: 2;
    cursor: pointer;
    color: black;
   
    
}



.selected-controller {
    -webkit-transition: border 0.5s ease-out;
    transition: border 0.5s ease-out;
    border-bottom: 2px solid orange;
}

.box-container {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    max-width: 24em;
    min-width: 21em;
    box-shadow: 0px 0px 20px 0px rgba(15, 15, 15, 0.2);
    border-radius: 6px;
    padding: 24px;
}

.inner-container {
    -webkit-transition: visibility 0.2s ease-out;
    transition: visibility 0.2s ease-out;
}

.inner-container.show {
    visibility: visible;
}


.header {
    text-align: center;
    padding: 5px;
    margin-bottom: 17px;
    /* margin-left: 35px; */
    /* margin-right: 35px; */
    font-family: Ozxgen, sans-serif;
    font-size: 22px;
    border-bottom: 2px solid blue_theme;
}

.box {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: center;
            justify-content: center;
}

.input-group {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    margin-top: 5px;
    margin-bottom: 5px;
}


.login-label {
    font-family: Oxygen, sans-serif;
    font-size: 20px;
    font-weight: 600;
    padding-left: 5px;
}


.login-input {
    height: 2.1em;
    border-radius: 3px;
    border: 1px solid blue_theme;
    -webkit-transition: border 0.4s ease-out;
    transition: border 0.4s ease-out;
    padding: 13px;
    font-family: Oxygen, sans-serif;
    font-size: 19px;
    color: #3e3e42;
    background: rgba(15, 15, 15, 0.01);
}

.login-input:hover {
    border: 2px solid blue_theme;
}

.login-input:focus {
    border: 2px solid #6565e7;
    box-shadow: 0px 0px 20px rgba(15, 15, 15, 0.2);
}


.login-input::-webkit-input-placeholder {
    font-family: Oxygen, sans-serif;
    font-size: 16px;
    color: rgba(46, 213, 116, 0.839);
    color: rgba(15, 15, 15, 0.4);
}


.login-input::-ms-input-placeholder {
    font-family: Oxygen, sans-serif;
    font-size: 16px;
    color: rgba(46, 213, 116, 0.839);
    color: rgba(15, 15, 15, 0.4);
}


.login-input::placeholder {
    font-family: Oxygen, sans-serif;
    font-size: 16px;
    color: rgba(46, 213, 116, 0.839);
    color: rgba(15, 15, 15, 0.4);
}

.login-btn {
    padding: 2px 30px;
     background-color: #417cef; 
    border: 0;
    font-size: 18px;
    border-radius: 3px;
    font-family: Oxygen, sans-serif;
    background-color: rgba(15, 15, 15, 0.03);
    margin-top: 20px;
    border: 2px solid #417cef;
    -webkit-transition: background-color 0.3s ease-out;
    transition: background-color 0.3s ease-out;
    cursor: pointer;
}

.login-btn:hover,
.login-btn:focus {
    background-color: #417cef;
    color: white;
}

.danger-error {
    color: #e74c3c;
    font-size: 16px;
}


.password-state {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row;
    margin-top: 20px;
}

.pwd {
    height: 6px;
    -webkit-flex: 1 1;
            flex: 1 1;
    visibility: hidden;
}

.pwd-weak {
    background-color: #e74c3c;
}

.pwd-medium {
    background-color: #3498db;
}

.pwd-strong {
    background-color: #2ecc71;
}

.show {
    visibility: visible;
}

